.stationery-wip {
  padding: 2rem 0; }
  .stationery-wip .cog {
    font-size: 5rem; }
  .stationery-wip h2,
  .stationery-wip h3 {
    text-align: center; }
  .stationery-wip h2 {
    font-size: 2rem;
    margin: 0 0 1rem;
    font-weight: 600; }
  .stationery-wip h3 {
    font-size: 1.5rem;
    font-weight: 400;
    margin: 0 0 2rem; }

@media (min-width: 0) and (max-width: 414px) {
  .stationery-wip {
    padding: 3.5rem 0; }
    .stationery-wip h2 {
      font-size: 1.7rem; }
    .stationery-wip h3 {
      font-size: 1.2rem; } }



