section {
  background-color: #3b1879;
  padding: 6rem 0 9rem; }
  section .row {
    overflow: visible; }
    section .row .col {
      display: block;
      float: left;
      margin: 0;
      padding: 0;
      position: relative; }
  section .col-ad {
    width: 60%; }
    section .col-ad h1,
    section .col-ad p {
      color: rgba(255, 255, 255, 0.85); }
    section .col-ad h1 {
      font-weight: 900;
      font-size: 4rem;
      margin: 0 0 5rem; }
    section .col-ad p {
      font-weight: 100;
      font-size: 1.1rem;
      margin: 0 0 1.2rem; }
      section .col-ad p.quote {
        padding: 0;
        color: rgba(255, 255, 255, 0.1);
        font-weight: 900;
        margin: 0 0 1rem;
        font-size: 10rem;
        line-height: 2rem;
        position: relative;
        top: .7rem;
        font-family: Roboto; }
      section .col-ad p.author {
        margin: 0;
        font-size: .9rem;
        text-align: right;
        color: rgba(255, 255, 255, 0.4); }
    section .col-ad .ad {
      width: 100%;
      padding: 3rem 6.97rem 0 0; }
  section .col-feature {
    width: 40%; }
    section .col-feature .feature {
      padding: 8rem 0 5rem 2rem;
      width: 100%;
      float: right; }

@media (min-width: 0) and (max-width: 414px) {
  section {
    padding: 3rem 0; }
    section .col-ad {
      margin: 0 0 1rem; }
    section .ad h1 {
      margin: 0;
      text-align: center;
      font-size: 2.5rem; }
    section .ad .quotation {
      display: none; }
    section .col.col-feature .feature {
      padding: 0 0 5rem; } }

@media (min-width: 415px) and (max-width: 968px) {
  section {
    padding: 6rem 0 18rem; }
    section .row .col .ad h1 {
      font-size: 2rem; } }

@media (min-width: 0) and (max-width: 968px) {
  section {
    height: auto; }
    section .row .col {
      width: 100%; }
      section .row .col.col-ad {
        margin: 0 0 3.5rem; }
      section .row .col .ad {
        padding-left: 0;
        padding-right: 0; }
        section .row .col .ad p.quote {
          font-size: 6rem;
          line-height: .7rem;
          top: -.1rem; }
        section .row .col .ad p.phrase {
          font-size: 1.2rem; }
      section .row .col .feature {
        padding: 0;
        width: 100%; } }



