.rate-details {
  margin: 0 0 1.8rem 0; }
  .rate-details p {
    margin: 0 0 .4rem;
    color: #726eab;
    font-size: .8rem; }
    .rate-details p:last-child {
      margin: 0; }
    .rate-details p svg {
      font-size: 1.2rem;
      position: relative;
      top: -.2rem;
      margin: 0 .4rem 0 0; }

@media (min-width: 0) and (max-width: 968px) {
  .rate-details {
    margin: 0 0 2rem; } }



